.popup {
    background: white;
    overflow: auto;
    height: 100%;
}

.popupWrapper {
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    margin-left: 0%;
    -webkit-box-shadow: 0px 0px 10px black;
            box-shadow: 0px 0px 10px black;
}

.hide {
    margin-left: -100%;
}

.popupContent {
    height: calc(100% - 50px);
    overflow-y: auto;
}

.closeButton {
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
    background: #e5efff;
    -webkit-box-shadow: 0px 0px 3px gray;
            box-shadow: 0px 0px 3px gray;
    height: 50px;
    cursor: pointer;
    padding-top: 13px;
    padding-left: 14px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


